import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import './BoardInsertForm.css'
import DraftEditor, { EditorState } from 'react-draft-wysiwyg';

const BoardInsertForm = ({ onInsert }) => {

    // state 
    const [title, setTitle] = useState('');
    const [writer, setWriter] = useState('');
    const [content, setContent] = useState('');
    const [files, setFiles] = useState(null);     //  files state 

    const handleChangeTitle = (e) => {
        setTitle(e.target.value)
    }

    const handleChangeWriter = (e) => {
        setWriter(e.target.value)
    }

    const handleChangeContent = (e) => {
        setContent(e.target.value)
    }

    //    
    const handleFileChange = (e) => {
        setFiles(e.target.files);
    };

    const onSubmit = () => {
        const formData = new FormData();
        formData.append('title', title);
        formData.append('writer', writer);
        formData.append('content', content);

        const headers = {
            headers: {
                'Content-Type' : 'multipart/form-data',
            },
        };
        
        if (files) {
            for (let i = 0; i < files.length; i++) {
                formData.append(`files[${i}]`, files[i]);
            }
        }

        // onInsert(title, writer, content)
        // onInsert({title,writer,content,files}, headers)      // formData  
        onInsert(formData, headers)                             // formData  
        
    }


    return (
        <div className='container'>
            <h1 className='title'> </h1>
            <table className='table'>
                <tbody>
                    <tr>
                        <td></td>
                        <td>
                            <input  type="text" 
                                    className='form-input'
                                    value={title} 
                                    onChange={handleChangeTitle} />
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>
                            <input  type="text" 
                                    className='form-input'
                                    value={writer} 
                                    onChange={handleChangeWriter} />
                        </td>
                    </tr>
                    <tr>
                        <td colSpan={2}></td>
                    </tr>
                    <tr>
                        <td colSpan={2}>
                            <textarea   cols="40" rows="10"
                                        className='form-input'
                                        value={content}
                                        onChange={handleChangeContent}
                            ></textarea>
                        </td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>
                        <input type='file' onChange={handleFileChange} multiple />
                        </td>
                    </tr>

                </tbody>
            </table>

            <div className='btn-box'>
                <Link to="/boards" className='btn'></Link>
                <button onClick={ onSubmit } className='btn'></button>
            </div>

        </div>
    )
}

export default BoardInsertForm